/* manrope regular (400) */
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/manrope_latin_cyrillic-regular.woff2") format("woff2"),
    url("../fonts/manrope_latin_cyrillic-regular.woff") format("woff");
}
/* manrope bold (800) */
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: url("../fonts/manrope_latin_cyrillic-800.woff2") format("woff2"),
    url("../manrope_latin_cyrillic-800.woff") format("woff");
}
