@charset "UTF-8";
/* @media */
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-family: "Manrope", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1;
  color: #000;
  background-color: #fff;
  margin: 0;
  min-width: 320px; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

picture {
  display: block; }

/* manrope regular (400) */
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/manrope_latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/manrope_latin_cyrillic-regular.woff") format("woff"); }

/* manrope bold (800) */
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: url("../fonts/manrope_latin_cyrillic-800.woff2") format("woff2"), url("../manrope_latin_cyrillic-800.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  padding: 0 3%; }

@media (min-width: 768px) {
  .container {
    padding: 0 6%; } }

@media (min-width: 1300px) {
  .container {
    max-width: 1300px;
    padding: 0 8%;
    margin: 0 auto; } }

.button {
  /*  Заменить градиенты */
  display: block;
  padding: 4.5%;
  text-align: center;
  font-size: 5.5vw;
  color: #fff;
  background: linear-gradient(271.59deg, #45BCC4 0%, #60C56A 100%); }

.header {
  padding-top: 10px; }

.header__top {
  display: flex;
  flex-direction: column; }

.header__logo {
  align-self: flex-end;
  margin-bottom: 5%;
  width: 37%; }

.navigation {
  display: flex;
  flex-direction: column; }

.navigation__list {
  display: none; }

.navigation__contacts {
  align-self: flex-end; }

.navigation__number {
  color: #000;
  font-weight: bold;
  font-size: 4vw;
  line-height: 2; }

.navigation__city {
  margin: 0;
  font-size: 3.5vw;
  line-height: 1.3;
  color: #2fbcb1;
  text-align: right; }

.promo {
  padding-top: 5%;
  padding-bottom: 15%; }

.promo__picture {
  margin-bottom: 8%; }

.promo__title {
  margin: 0 0 5% 0;
  font-size: 13vw; }

.promo__text {
  margin: 0 0 8% 0;
  font-size: 4.9vw;
  line-height: 1.2;
  font-weight: 800; }

.about-us__title {
  font-size: 10vw;
  text-align: center;
  margin: 0 0 10% 0; }

.about-us__user {
  text-align: center;
  margin-bottom: 10%; }

.about-us__picture {
  margin-bottom: 2%; }

.about-us__user-name,
.about-us__user-text,
.about-us__studio-information {
  margin: 0; }

.about-us__user-name {
  font-size: 5vw;
  line-height: 1.5; }

.about-us__user-text {
  color: #4f4f4f;
  font-size: 3.5vw;
  line-height: 1.5; }

.about-us__studio-information {
  font-size: 3.7vw;
  line-height: 1.6;
  padding: 7% 4%;
  margin: 0 4%;
  border: 1px solid #dddddd;
  border-radius: 10px;
  position: relative; }
  .about-us__studio-information::before, .about-us__studio-information::after {
    content: "";
    position: absolute;
    width: 21%;
    height: 21%;
    background-image: url("../img/quotes.jpg");
    background-repeat: no-repeat;
    background-size: contain; }
  .about-us__studio-information::before {
    top: -14%;
    left: 2%; }
  .about-us__studio-information::after {
    transform: rotate(-180deg);
    bottom: -12%;
    right: 2%; }

.services {
  padding: 15% 5%; }

.services__title {
  font-size: 10vw;
  margin: 0 0 10% 0;
  text-align: center; }

.services__picture {
  text-align: center;
  margin-bottom: 5%; }

.services__img {
  width: 92%;
  height: auto; }

.services__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column; }

.services__item {
  padding: 5% 4%;
  margin: 0 0 5% 0; }

.services__subtitle {
  font-size: 6vw;
  margin: 0 0 8% 0; }

.services__item-price {
  margin: 0;
  font-size: 10vw;
  line-height: 1.6;
  font-weight: 800;
  color: #F04D37;
  text-align: center; }

.services__table {
  display: block;
  border-collapse: collapse;
  margin: 0 0 10% 0; }

.services__table-body {
  display: block; }

.services__table-row {
  display: flex;
  flex-direction: column;
  padding: 10% 0;
  border-bottom: 0.3vh solid #cdcdcd; }
  .services__table-row:last-child {
    border-bottom: none; }

.services__table-data {
  margin-bottom: 3%; }

.services__table-price {
  font-weight: 800; }

.services__table-cell {
  font-size: 6.6vw;
  line-height: 1.1; }

.text-list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin: 0 0 6% 0; }

.text-list__item {
  margin-bottom: 3%;
  font-size: 4vw;
  line-height: 1.3; }
  .text-list__item--marked {
    color: #ff0303; }

.advantages {
  padding-bottom: 5%; }

.advantages__title {
  font-size: 10vw;
  text-align: center;
  margin: 0 0 10% 0; }

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.advantages__item {
  padding: 70px 7% 10%;
  margin-bottom: 5%;
  position: relative; }
  .advantages__item:last-child {
    margin-bottom: 0; }
  .advantages__item::before {
    content: "";
    position: absolute;
    top: 6%;
    left: 7%;
    width: 43%;
    height: 39%;
    background-size: contain;
    background-repeat: no-repeat; }
  .advantages__item:first-child::before {
    background-image: url("../img/icon/01.svg");
    width: 39%;
    height: 53%; }
  .advantages__item:nth-child(2)::before {
    background-image: url("../img/icon/02.svg"); }
  .advantages__item:last-child::before {
    background-image: url("../img/icon/03.svg"); }

.advantages__text {
  margin: 0;
  font-size: 4.3vw;
  line-height: 1.2; }

.steps {
  padding: 0 7% 5% 0; }

.steps__title {
  margin: 0 0 10% 0;
  font-size: 10vw;
  text-align: center; }

.steps__list {
  margin: 0;
  padding: 0;
  list-style: none;
  counter-reset: pages; }

.steps__item {
  font-size: 5vw;
  line-height: 1.2;
  margin-bottom: 6%;
  padding-left: 15%;
  position: relative;
  counter-increment: pages; }
  .steps__item::before {
    display: flex;
    justify-content: center;
    align-items: center;
    content: counter(pages);
    position: absolute;
    top: 0;
    left: 4%;
    width: 7vw;
    height: 7vw;
    border-radius: 50%;
    color: #fff;
    background: linear-gradient(271.59deg, #45BCC4 0%, #60C56A 100%); }

.offer__title {
  margin: 0 0 10% 0;
  font-size: 10vw;
  text-align: center; }

.offer__information {
  max-width: 620px;
  margin: 0 auto; }

.offer__img {
  border-radius: 10px;
  width: 100%; }
  .offer__img--gift {
    margin-bottom: 10%; }
  .offer__img--cashback {
    margin-bottom: 5%; }
  .offer__img--logo-sale {
    margin-bottom: 10%; }

.contacts {
  padding: 10% 0; }

.contacts__title {
  font-size: 10vw;
  margin: 0 0 10% 0;
  text-align: center; }

.contacts__text {
  font-size: 5vw;
  line-height: 1.2;
  margin: 0 0 10% 0; }

.contacts__list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-bottom: 10%; }

.contacts__item {
  padding-left: 10%;
  margin-bottom: 4%;
  position: relative; }
  .contacts__item::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 5vw;
    height: 5vw;
    background-size: contain;
    background-repeat: no-repeat; }
  .contacts__item:first-child::before {
    background-image: url("../img/icon/phone.png"); }
  .contacts__item:nth-child(2)::before {
    background-image: url("../img/icon/mail.png"); }
  .contacts__item:last-child::before {
    background-image: url("../img/icon/location.png");
    width: 7vw;
    height: 7vw;
    left: -1%;
    top: -30%; }

.contacts__number,
.contacts__mail {
  color: #000; }

.contacts__address {
  margin: 0; }

.feedback {
  padding: 10% 0 15% 0;
  border-bottom: 0.2vw solid #818181; }

.feedback__item {
  margin-bottom: 7%; }

.feedback__label {
  display: block;
  font-size: 5vw;
  margin-bottom: 3%; }

.feedback__input,
.feedback__area {
  display: block;
  width: 100%;
  padding: 3.2% 2% 3.2% 4%;
  font-size: 6vw;
  border: 0.2vw solid #818181; }
  .feedback__input::placeholder,
  .feedback__area::placeholder {
    color: #cecece; }

.feedback__area {
  resize: vertical;
  min-height: 90px;
  width: 100%;
  font-size: 5vw; }

.feedback__button {
  border: none;
  width: 100%; }

.footer {
  padding-bottom: 5%; }

.footer__link {
  color: #828282;
  margin-bottom: 10%;
  display: block;
  font-size: 5vw; }

.footer__social-list {
  margin: 0;
  padding: 0;
  list-style: none; }

.footer__social-link {
  display: block;
  font-size: 4.5vw;
  line-height: 1.6;
  color: #828282;
  padding-left: 10%;
  position: relative; }
  .footer__social-link::before {
    content: "";
    position: absolute;
    top: -15%;
    left: -2%;
    width: 135%;
    height: 135%;
    background-image: url("../img/icon/instagram.png");
    background-repeat: no-repeat;
    background-size: contain; }
